.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

form.search {
  overflow: auto;
  margin-bottom: 10px;
}

form.search button[type="submit"] {
  /* float: right; */
  margin-top: 10px;
}

.form-group {

  /* margin-left: 40px;
  margin-right: 40px; */

}

h3 {
  font-weight: bold;
}

.form-control {
  height: 50px;
  font-size: 20px;
}

table {
  border-spacing: 0;
  width: 100%;
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 16px;
}

tr {
  background-color: white;
  font-size: 20px;
  border-radius: 10px;
  width: 100%;
}

tbody tr:nth-child(odd){
  background-color: lightgray;
  color:black;
  border-radius: 10px;
}

.btn {
  
  margin-right: 15px;
  width: 20%;
 
}

.card {
  border-radius: 10px;
  margin-bottom: 10px;
}

.card-img-top {

  border-top-left-radius: 10px;

  border-top-right-radius: 10px;

}




